.node {
    cursor: pointer;
  }
  
  .node circle {
    fill: #fff;
    stroke: steelblue;
    stroke-width: 1.5px;
  }
  
  .node text {
    font: 10px sans-serif;
  }
  
  .link {
    fill: none;
    stroke: #ccc;
    stroke-width: 1.5px;
  }
  
  .found {
    fill: #ff4136;
    stroke: #ff4136;
  }
  
  .search {
    float: left;
    font: 10px sans-serif;
    width: 30%;
  }
  
  ul.select2-results {
   max-height: 100px;
  }
  
  .select2-container,
  .select2-drop,
  .select2-search,
  .select2-search input {
    font: 10px sans-serif;
  }
  
  #block_container {
    display: inline;
  }
  .link {
	  fill: none;
	  stroke: #ccc;
	  stroke-width: 1.5px;
	}
  #icd-title {
    margin-top: 75px;
    margin-bottom: 0;
  }